import { useEffect, useState } from "react";
import {
  useLmsConfigMaster,
  useLmsUpdateSetoffRule,
} from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import QvipleLoader from "../../../Loader/QvipleLoader";

const LmsSettoffRule = ({ lcid, instituteId, leaveTypeToggle }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    casual_leave: "",
    medical_leave: "",
    sick_leave: "",
    commuted_leave: "",
    maternity_leave: "",
    paternity_leave: "",
    quarantine_leave: "",
    half_pay_leave: "",
    study_leave: "",
    sabbatical_leave: "",
    special_disability_leave: "",
    winter_vacation_leave: "",
    summer_vacation_leave: "",
    child_adoption_leave: "",
    bereavement_leave: "",
    earned_leave: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [lmsUpdateSetoffRule] = useLmsUpdateSetoffRule();

  const { lmsConfigMaster, lmsConfigMasterRefetch } = useLmsConfigMaster({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });

  useEffect(() => {
    if (lmsConfigMaster?.set_off_rules) {
      setState({
        casual_leave: lmsConfigMaster?.set_off_rules?.casual_leave ?? "",
        medical_leave: lmsConfigMaster?.set_off_rules?.medical_leave ?? "",
        sick_leave: lmsConfigMaster?.set_off_rules?.sick_leave ?? "",
        commuted_leave: lmsConfigMaster?.set_off_rules?.commuted_leave ?? "",
        maternity_leave: lmsConfigMaster?.set_off_rules?.maternity_leave ?? "",
        paternity_leave: lmsConfigMaster?.set_off_rules?.paternity_leave ?? "",
        quarantine_leave:
          lmsConfigMaster?.set_off_rules?.quarantine_leave ?? "",
        half_pay_leave: lmsConfigMaster?.set_off_rules?.half_pay_leave ?? "",
        study_leave: lmsConfigMaster?.set_off_rules?.study_leave ?? "",
        sabbatical_leave:
          lmsConfigMaster?.set_off_rules?.sabbatical_leave ?? "",
        special_disability_leave:
          lmsConfigMaster?.set_off_rules?.special_disability_leave ?? "",
        winter_vacation_leave:
          lmsConfigMaster?.set_off_rules?.winter_vacation_leave ?? "",
        summer_vacation_leave:
          lmsConfigMaster?.set_off_rules?.summer_vacation_leave ?? "",
        child_adoption_leave:
          lmsConfigMaster?.set_off_rules?.child_adoption_leave ?? "",
        bereavement_leave:
          lmsConfigMaster?.set_off_rules?.bereavement_leave ?? "",
        earned_leave: lmsConfigMaster?.set_off_rules?.earned_leave ?? "",
      });
    }
  }, [lmsConfigMaster?.set_off_rules]);

  const onUpdate = (e) => {
    if (+e.target.value) {
      setState((prev) => ({
        ...prev,
        [e.target.name]: +e.target.value,
      }));
    }
  };

  const onSave = () => {
    if (lcid) {
      setDisabled((pre) => !pre);
      lmsUpdateSetoffRule({
        lcid: lcid,
        setOffRule: { set_off_rules: state },
      })
        .then(() => {
          lmsConfigMasterRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  // console.info("sfsdf", lmsConfigMaster);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("leave_setoff_rule")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      <UserMemberContentWrapper>
        <div className={style.lms_rule_container}>
          {leaveTypeToggle["casual_leave"] && (
            <FormInput
              label={t("casual_leave")}
              placeholder={t("casual_leave_placeholder")}
              name={"casual_leave"}
              value={state?.casual_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["medical_leave"] && (
            <FormInput
              label={t("medical_leave")}
              placeholder={t("medical_leave_placeholder")}
              name={"medical_leave"}
              value={state?.medical_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["sick_leave"] && (
            <FormInput
              label={t("sick_leave")}
              placeholder={t("sick_leave_placeholder")}
              name={"sick_leave"}
              value={state?.sick_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["commuted_leave"] && (
            <FormInput
              label={t("commuted_leave")}
              placeholder={t("commuted_leave_placeholder")}
              name={"commuted_leave"}
              value={state?.commuted_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["maternity_leave"] && (
            <FormInput
              label={t("maternity_leave")}
              placeholder={t("maternity_leave_placeholder")}
              name={"maternity_leave"}
              value={state?.maternity_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["paternity_leave"] && (
            <FormInput
              label={t("paternity_leave")}
              placeholder={t("paternity_leave_placeholder")}
              name={"paternity_leave"}
              value={state?.paternity_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["quarantine_leave"] && (
            <FormInput
              label={t("quarantine_leave")}
              placeholder={t("quarantine_leave_placeholder")}
              name={"quarantine_leave"}
              value={state?.quarantine_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["half_pay_leave"] && (
            <FormInput
              label={t("half_pay_leave")}
              placeholder={t("half_pay_leave_placeholder")}
              name={"half_pay_leave"}
              value={state?.half_pay_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["study_leave"] && (
            <FormInput
              label={t("study_leave")}
              placeholder={t("study_leave_placeholder")}
              name={"study_leave"}
              value={state?.study_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["sabbatical_leave"] && (
            <FormInput
              label={t("sabbatical_leave")}
              placeholder={t("sabbatical_leave_placeholder")}
              name={"sabbatical_leave"}
              value={state?.sabbatical_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["special_disability_leave"] && (
            <FormInput
              label={t("special_disability_leave")}
              placeholder={t("special_disability_leave_placeholder")}
              name={"special_disability_leave"}
              value={state?.special_disability_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["winter_vacation_leave"] && (
            <FormInput
              label={t("winter_vacation_leave")}
              placeholder={t("winter_vacation_leave_placeholder")}
              name={"winter_vacation_leave"}
              value={state?.winter_vacation_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["summer_vacation_leave"] && (
            <FormInput
              label={t("summer_vacation_leave")}
              placeholder={t("summer_vacation_leave_placeholder")}
              name={"summer_vacation_leave"}
              value={state?.summer_vacation_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["child_adoption_leave"] && (
            <FormInput
              label={t("child_adoption_leave")}
              placeholder={t("child_adoption_leave_placeholder")}
              name={"child_adoption_leave"}
              value={state?.child_adoption_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["bereavement_leave"] && (
            <FormInput
              label={t("bereavement_leave")}
              placeholder={t("bereavement_leave_placeholder")}
              name={"bereavement_leave"}
              value={state?.bereavement_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}

          {leaveTypeToggle["leave_taken"] && (
            <FormInput
              label={t("earned_leave")}
              placeholder={t("earned_leave_placeholder")}
              name={"earned_leave"}
              value={state?.earned_leave}
              type="tel"
              onChange={onUpdate}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          )}
        </div>
        <CreateButton label={t("update")} onAction={onSave} />
        {disabled && <QvipleLoader />}
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};

export default LmsSettoffRule;
